@import "@/assets/scss/main.scss";

.user__profile__header {
  display: flex;
  padding: 16px 0;
  img {
    width: 17px;
    height: 14px;
    margin: 14px 20px 14px 28px;
  }
  .header__name {
    color: $scale-gray10;
    font-size: 18px;
    font-weight: 700;
  }
  .header__tweet__count {
    color: $secondary-gray;
    font-size: 13px;
    font-weight: 500;
  }
}
.user__profile__content {
  position: relative;
  .user__cover {
    height: 200px;
    object-fit: cover;
    object-position: center;
  }
  .user__avatar {
    position: absolute;
    top: 124px;
    left: 16px;
    width: 140px;
    height: 140px;
    border: 4px solid $scale-gray1;
    border-radius: 50%;
  }
  .profile__btn {
    position: absolute;
    top: 216px;
    right: 16px;
    display: flex;
    .message__btn,
    .notfi__btn {
      width: 50px;
      height: 40px;
    }
    .following__btn {
      width: 96px;
      height: 40px;
      margin-left: 5px;
      border-radius: 50px;
      background: $brand-orange;
      font-weight: 400;
      font-size: 16px;
      color: $scale-gray1;
    }
    .unfollowing__btn {
      width: 64px;
      height: 40px;
      margin-left: 5px;
      border-radius: 50px;
      border: 1px solid $brand-orange;
      background: $scale-gray1;
      font-weight: 400;
      font-size: 16px;
      color: $brand-orange;
    }
  }

  .modify__profile__btn {
    position: absolute;
    top: 216px;
    right: 16px;
    color: $brand-orange;
    border: 1px solid $brand-orange;
    border-radius: 50px;
    width: 128px;
    height: 40px;
    font-size: 16px;
    font-weight: 400;
  }
  .user__info {
    padding: 72px 16px 16px 16px;
    .user__name {
      color: $scale-gray10;
      font-size: 18px;
      font-weight: 700;
    }
    .user__account {
      color: $secondary-gray;
      font-size: 14px;
      font-weight: 400;
    }
    .user__introduction {
      color: $scale-gray10;
      font-size: 14px;
      font-weight: 400;
      margin: 8px 0;
    }
    .user__follow {
      display: flex;
      font-size: 14px;
      font-weight: 400;
      color: $secondary-gray;
      .user__follow__num {
        color: $scale-gray10;
      }
      .following {
        margin-right: 20px;
      }
    }
  }
}
