@import "@/assets/scss/main.scss";

.user__edit__modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(23, 23, 37, 0.4);
  z-index: 999;
  .modal-card {
    position: absolute;
    top: 56px;
    left: 50vw;
    transform: translate(-50%, 0);
    border-radius: 14px;
    background: $scale-gray1;
    width: 50vw;
    height: 60vh;
    max-width: 634px;
    min-height: 640px;
    .edit__header {
      display: flex;
      align-items: center;
      height: 57px;
      img {
        width: 15px;
        height: 15px;
        margin: 20px;
      }
      h5 {
        font-size: 18px;
        font-weight: 700;
      }
      .save {
        position: absolute;
        top: 8px;
        right: 16px;
        width: 64px;
        height: 40px;
        padding: 8px 16px;
        background-color: $brand-orange;
        border-radius: 50px;
        font-size: 16px;
        font-weight: 400;
        color: $scale-gray1;
      }
    }
    .user__edit__image {
      position: relative;
      .user__cover {
        .user__cover__img {
          height: 200px;
          object-fit: cover;
          object-position: center;
          opacity: 70%;
        }
        .user__cover__mask {
          background: rgba(23, 23, 37, 0.5);
          position: absolute;
          top: 0;
          height: 200px;
          width: 100%;
        }
        .user__cover__icons {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          z-index: 999;
          display: flex;
          width: 75px;
          justify-content: space-between;
          align-items: center;
        }
        .upload {
          width: 20px;
          height: 20px;
        }
        .cancel {
          width: 15px;
          height: 15px;
        }
      }
      .user__avatar {
        .user__avatar__img {
          position: absolute;
          top: 124px;
          left: 16px;
          height: 140px;
          width: 140px;
          border: 4px solid $scale-gray1;
          border-radius: 50%;
        }
        .user__avatar__mask {
          background: rgba(23, 23, 37, 0.5);
          position: absolute;
          top: 124px;
          left: 16px;
          height: 140px;
          width: 140px;
          border: 4px solid $scale-gray1;
          border-radius: 50%;
        }
        .upload {
          position: absolute;
          top: 200px;
          left: 86px;
          transform: translate(-50%, -50%);
          height: 20px;
          width: 20px;
        }
      }
    }
    .user__edit__content {
      padding: 60px 16px 40px 16px;
      label {
        padding: 2px 18px;
        color: $scale-gray8;
        font-size: 14px;
        font-weight: 400;
        line-height: 22px;
        background-color: $form-bg;
        margin: 0;
        border-radius: 2px 0;
      }
      input,
      textarea {
        padding: 0 18px 2px 18px;
        width: 100%;
        border: 0px;
        background-color: $form-bg;
        font-size: 16px;
        line-height: 26px;
        color: $scale-gray10;
        border-bottom: 2px solid $form-border;
        text-align: start;
        &.introduction {
          height: 147px;
          resize: none;
        }
        &:hover,
        :focus {
          border-bottom: 2px solid $sider-blue;
        }
        &:-webkit-autofill {
          -webkit-box-shadow: 0 0 0px 100px $form-bg inset;
        }
      }
      .compute {
        display: flex;
        justify-content: space-between;
        font-size: 12px;
        font-weight: 500;
        margin: 4px 0 8px 0;
        .words__compute--warn {
          color: $error-red;
        }
        .words__compute {
          color: $scale-gray8;
        }
      }
    }
  }
}

